import React, {useRef, useEffect, useState, useContext} from 'react'
import AppContext from '../contexts/AppContext.js';

const MainTitle = ({appWidth}) => {
    
    // get viewIntro
    const {viewIntro} = useContext(AppContext);
    
    // set up refs
    const containerRef = useRef(null);
    const borderRef = useRef(null);
    const orientationRef = useRef(null);
    const databaseRef = useRef(null);
    
    // set up useStates
    const [borderWidth, setBorderWidth] = useState(0);
    const [orientationWidth, setOrientationWidth] = useState(0);
    const [databaseWidth, setDatabaseWidth] = useState(0);
    // const [borderPosition, setBorderPosition] = useState(0);
    // const [orientationPosition, setOrientationPosition] = useState(0);
    // const [databasePosition, setDatabasePosition] = useState(0);
    const [containerLeft, setContainerLeft] = useState(0);
    // const [prevAppWidth, setPrevAppWidth] = useState(appWidth); // 0 on first render
    
    const [translations, setTranslations] = useState({});
    
    // space between words (px)
    const spaceWidth = 50;
    
    let {translateBorder, translateOrientation, translateDatabase} = translations;
    
    // resize effect only sets values
    const handleResize = ()=>{
        console.log("resize");
        // check widths in case font size has changed
        setBorderWidth(borderRef.current.getBoundingClientRect().width)
        setOrientationWidth(orientationRef.current.getBoundingClientRect().width); 
        setDatabaseWidth(databaseRef.current.getBoundingClientRect().width);
        setContainerLeft(containerRef.current.getBoundingClientRect().left); 
        
    }
    
    // initial render useEffect sets initial values
    useEffect(()=>{
        // can't guarantee these values are available further below -- when they change plan to call another effect
        setBorderWidth(borderRef.current.getBoundingClientRect().width)
        setOrientationWidth(orientationRef.current.getBoundingClientRect().width); 
        setDatabaseWidth(databaseRef.current.getBoundingClientRect().width); 

        setContainerLeft(containerRef.current.getBoundingClientRect().left);
        // setPrevAppWidth(appWidth);
        
        // add resize eventListener
        window.addEventListener('resize', handleResize);
        return ()=>{
            window.removeEventListener('resize', handleResize);
        };
    },[]); // only on first render
    
    // calculates positions for title, is called whenever appWidth changes
    useEffect(()=>{

        const totalTitleWidth = 2*spaceWidth + borderWidth + orientationWidth + databaseWidth;
        
        const newBorderPosition = (appWidth - totalTitleWidth)/2;
        const newOrientationPosition = newBorderPosition + borderWidth + spaceWidth;
        const newDatabasePosition = newOrientationPosition + orientationWidth + spaceWidth;
        
        translateBorder = newBorderPosition - containerLeft;
        translateOrientation = newOrientationPosition - containerLeft;
        translateDatabase = newDatabasePosition - containerLeft;
        setTranslations({translateBorder, translateOrientation, translateDatabase});

        // window.addEventListener('resize', handleResize);
        // return ()=>{
        //     window.removeEventListener('resize', handleResize);
        // };
        
    },[appWidth, borderWidth, orientationWidth, databaseWidth, containerLeft]); // use this effect when appWidth or any of the main title measurements change
    
    // useEffect(()=> {
    //     // add resize eventListener
    //     window.addEventListener('resize', handleResize);
    //     // clean up
    //     return ()=>{
    //         window.removeEventListener('resize', handleResize);
    //     }
    // },)
    
    
    return (
        <div className="main-title-container" ref={containerRef}>
        {/* <h1 className="main-title">Border<br></br>Orientation<br></br>Database</h1> */}
        <div className="main-title-word main-title__border" ref={borderRef}style={{"--appwidth":`${appWidth}px`, "--translate-value":`${translateBorder}px`}}>Border</div>
        <div className="main-title-word main-title__orientation" ref={orientationRef} style={{"--appwidth":`${appWidth}px`, "--translate-value":`${translateOrientation}px`}}>Orientation</div>
        <div className="main-title-word main-title__database" ref={databaseRef} style={{"--appwidth":`${appWidth}px`, "--translate-value":`${translateDatabase}px`}}>Database</div>
        </div>
        )
    }
    
    export default MainTitle
