import React, {useContext, useState} from 'react'
import AppContext from '../contexts/AppContext'

const MorphBtn = () => {

    const {morphEarth, inVisualizeDataMode, earthIsFlat, setEarthIsFlat} = useContext(AppContext);
    // const [earthIsFlat, setEarthIsFlat] = useState(false);

    const handleClick = ()=>{
        morphEarth(earthIsFlat);
        setEarthIsFlat(!earthIsFlat);
    };

    return (

            <button  className={inVisualizeDataMode? 'title-btn morph-btn morph-btn--show' : 'title-btn morph-btn'} onClick={handleClick}>Toggle 3D</button>
    )
}

export default MorphBtn
