import React, {useContext, useRef} from 'react'
import AppContext from '../contexts/AppContext.js';

const CloseIntroBtn = () => {
    
    const {viewIntro, setViewIntro, setIsExpoPlaying, removeExpoComponents} = useContext(AppContext);
    const btnRef = useRef(null);
    
    const handleClick = ()=>{
        setIsExpoPlaying(false);
        setViewIntro(!viewIntro);
        removeExpoComponents();
        window.scrollTo(0,0);
    }
    
    return (
        <button  className={viewIntro? 'title-btn close-intro-btn close-intro-btn--show' : 'title-btn close-intro-btn'} onClick={handleClick} ref={btnRef}>Close</button>
        )
    }
    
    
    export default CloseIntroBtn
