export const introParagraph1Text = 'Border orientation describes the \nextent to which a modern state is \ncommitted to the spatial display of \nborder control and provides a lens \nthrough which to analyze resistance to \nglobalization, growing populism, and \nthe consequences of intensified \nborder politics. Like other concepts \nsuch as ideology or \nentrepreneurialism, border \norientation is a latent trait that \ncannot be observed directly. Instead, \nwe measure border orientation using \nnovel, geo-spatial data on the built \nenvironment along the worlds \nborders and theorize that real and \nperceived pressures of globalization \nhave resulted in more controlling forms \nof border governance.\n\nIn an article entitled Border \nOrientation in a Globalizing World, we \nsuggest three observable components \nof this desire to filter movement across \nstate borders:\n\nState infrastructural investments include \nstructures to control movements at \nborder crossings (split lanes for \ninspection of persons and vehicles, \ngates that can be closed to control \ntraffic, and official buildings where \npersonnel and equipment can be \nstationed).\n\nBorder walls and fences are permanent \nstructures located near \nor along an international border and \ndesigned to block vehicular or \npedestrian traffic (excluding animal \ncontainment fences).\n\nWe account for police presence in close \nproximity of the border by using the \ndensity of police stations within a 30 km \nborder zone. Police station density is \ncalculated per capita and relative to its \ndensity at the interior of the country.\n\nThese three components are used in our \nregression to generate a border \norientation score for each crossing \ngeolocated using the intersections of \nhighways and international borders. These \nscores are used to generate an estimate of \noverall border orientation that can be \ncalculated at a specific border crossing, \nalong an international border, and for a \ncountry as a whole.';

