export const norwayRussiaText = 'The Storskog border crossing is the sole border \ncrossing between Norway and Russia, located \nat 69.657617, 30.204027 and is an entryway to \nthe Schengen region. It provides a clear \nexample of split lanes indicative of the ability to \nseparate traffic for closer inspection. This \ncrossing is also notable for its relative \nsymmetry on each side, and scores about a 1.9 \non a scale of -3 to 3 on our border orientation \nscale.'

export const chilePeruText = 'The border crossing at Tacna (Peru) and Arica \n(Chile) located at -18.310554, -70.314099 is \nnotable for its high score on very localized \ninfrastructure (especially buildings and covered \ngates) but its complete lack of co-located \nfencing to prevent people from simply going \naround the inspection infrastructure.'

export const omanUAEText = 'The border crossing between Oman and the UAE, located near \nShikla UAE at 24.21471, 55.95516 has undergone rapid development. \nIn this example, there is no evidence of border infrastructure on \nthe Oman side of the border in 2014, but we can see it appear in \n2016 and view its completion by 2019. A huge police station is \nnotable as part of the border security presence on the Oman side \n(far right).'

