export const introConclusionText = 'Our datasets document and quantify these \naspects of border orientation. The Border \nCrossings of the World (BCW) dataset \ncontains information about infrastructural \ninvestments at border crossings and is \ncomposed of 1,487 geolocated border \ncrossings - indicated as where a highway \ncrosses an international border - observed \nbetween 1993 and 2020. The Border Walls \nof the World (BWW) dataset geolocates \nborder walls or fences along international \nborders. Both datasets are coded yearly, \nand can be found and visualized on this site.'