import React, {useContext, useState, useEffect} from 'react'
import AppContext from '../contexts/AppContext';

const Slider = () => {
    const {minYear, maxYear, updateVisualizationByYear, inVisualizeDataMode} = useContext(AppContext);
    const [value, setValue] = useState(maxYear);
    const handleChange = (event)=>{
        // set value
        setValue(event.target.value);
        // updateCrossingColors
        updateVisualizationByYear(event.target.value);
        // update border colors
    }


    return (
        <div className={inVisualizeDataMode? 'slider-container slider-container--show' : 'slider-container'}>
            <input type="range" min={`${minYear}`} max={`${2017}`} onChange={handleChange} className="slider" />
        </div>
    )
}

export default Slider
