import * as THREE from './external_js/three.module.js'

class StraightCurve extends THREE.Curve{
    constructor (from, to, magnitude, scale = 1){
        super();

        this.scale = scale;
        this.from = from;
        this.to = to;
        this.magnitude = magnitude;
    }

    getPoint(t, optionalTarget = new THREE.Vector3()) {
        const points =[];
        const [x1, y1, z1] = this.from;
        const [x2, y2, z2] = this.to;
        const dx = this.magnitude*(x2 - x1);
        const dy = this.magnitude*(y2 - y1);
        const dz = this.magnitude*(z2 - z1);
        const tx = t*dx + x1;
        const ty = t*dy + y1;
        const tz = t*dz + z1;

        return optionalTarget.set( tx, ty, tz ).multiplyScalar( this.scale );
    }
}

export default StraightCurve;