function genParagraphMesh(font, text) {
    const textShapes = font.generateShapes(text);
    const textGeometry = new THREE.ShapeGeometry(textShapes);
    
    const textMaterial = new THREE.MeshBasicMaterial({color: 0xFFFFFF,
        transparent: true});
        
        const textMesh = new THREE.Mesh( textGeometry, textMaterial);
        
        return textMesh;
    }
    
    function genCircleMesh(borderID, vertexCrossingIDMap, flatPos) {
        let crossing1Position;
        if (flatPos) {
            crossing1Position = flatPos;
        } else {
            vertexCrossingIDMap.forEach((crossing)=>{
                // get position from map
                // if(crossing.crossingID === borderID) crossing1Position = crossing.coordinates;
                if(crossing.crossingID === borderID) crossing1Position = crossing.vertices.array;
            })
        }
        
        
        const circleGeometry = new THREE.RingGeometry( .06, .10, 32 );
        
        // const [x, y, z] = crossing1Position;
        const x = crossing1Position[0];
        const y = crossing1Position[1];
        const z = crossing1Position[2];
        
        const circleMaterial = new THREE.MeshBasicMaterial( { color: 0xffff00, side: THREE.DoubleSide } );
        const circleMesh = new THREE.Mesh( circleGeometry, circleMaterial );
        
        // lookAt the point you are going to move it to make it tangent to the sphere at that point
        // rotate before translating
        // circleMesh.lookAt(rotX, rotY, rotZ);
        // circleMesh.position.x = rotX;
        // circleMesh.position.y = rotY;
        // circleMesh.position.z = rotZ;
        if (flatPos) {
            circleMesh.position.x = x;
            circleMesh.position.y = y;
            circleMesh.position.z = z + .005;
            circleMesh.lookAt(x, y, 10);
        } else {
            circleMesh.lookAt(x, y, z);
            circleMesh.position.x = 1.005*x;
            circleMesh.position.y = 1.005*y;
            circleMesh.position.z = 1.005*z;
        }
        
        
        
        return circleMesh;
    }
    
    function genCrossingPicture(img,width, height) {
        const textureLoader = new THREE.TextureLoader();
        const imgTexture = textureLoader.load(img);
        const imgGeometry = new THREE.PlaneGeometry(width, height);
        const imgMaterial = new THREE.MeshBasicMaterial({map: imgTexture, transparent: true,
            opacity: 1,
            side: THREE.DoubleSide});
            const imgMesh = new THREE.Mesh(imgGeometry, imgMaterial);
            imgMesh.scale.x = .01;
            imgMesh.scale.y = .01;
            imgMesh.scale.z = .01;
            
            return imgMesh;
        }
        
        function genGlowMesh(alphaMap, width, height) {
            const textureLoader = new THREE.TextureLoader();
            const map = textureLoader.load(alphaMap); 
            // const glowGeometry = new THREE.PlaneGeometry(width + .65, height + .65);
            const glowGeometry = new THREE.PlaneGeometry(width + .45, height + .38);
            const glowMaterial = new THREE.MeshBasicMaterial(
                {
                    color: 0xFFFFFF,
                    transparent: true,
                    alphaMap: map
                });
                const glowMesh = new THREE.Mesh(glowGeometry, glowMaterial);
                
                glowMesh.scale.x = .01;
                glowMesh.scale.y = .01;
                glowMesh.scale.z = .01;
                
                return glowMesh;
            }
            
            function getGrowableLine(from, to, magnitude) {
                
                const path = new StraightCurve(from, to, magnitude);
                const geometry = new THREE.TubeGeometry(path, 256, .008, 8, true);
                const material = new THREE.MeshBasicMaterial( { color: 0x00ff00 } );
                const line = new THREE.Mesh( geometry, material );
                
                return line;
            }
            
            export {initTourComponents, genParagraphMesh, genCircleMesh, genCrossingPicture, genGlowMesh, getGrowableLine};