import React, { useState, useContext } from "react";
// external imports 

const AppContext = React.createContext();

export const AppProvider = ({ children }) => {
  const [appWidth, setAppWidth] = useState(window.innerWidth);
  const [appHeight, setAppHeight] = useState(window.innerHeight);
  // const [appWidth, setAppWidth] = useState(0);
  const [viewIntro, setViewIntro] = useState(false);
  const [isExpoPlaying, setIsExpoPlaying] = useState(false);
  const [atBottomOfDoc, setAtBottomOfDoc] = useState(false);
  const [minYear, setMinYear] = useState(0);
  const [maxYear, setMaxYear] = useState(0);
  const [earthIsFlat, setEarthIsFlat] = useState(false);
  const [sectionRefs, setSectionRefs] = useState({});
  const [inEditMode, setInEditMode] = useState(true);

  // three js function states
  const [scrollEarth, setScrollEarth] = useState(()=>()=>console.log('scrollEarth not set'));
  const [removeExpoComponents, setRemoveExpoComponents] = useState(()=>()=>console.log('removeExpoComponents not set'));
  const [enterVisualizeDataMode, setEnterVisualizeDataMode] = useState(()=>()=>console.log('enterVisualizeDataMode not set'));
  const [exitVisualizeDataMode, setExitVisualizeDataMode] = useState(()=>()=>console.log('exitVisualizeDataMode not set'));
  // const [updateCrossingColors, setUpdateCrossingColors] = useState(()=>()=>console.log('updateCrossingColors not set'));
  const [updateVisualizationByYear, setUpdateVisualizationByYear] = useState(()=>()=>console.log('updateVisualizationByYear not set'));
  const [morphEarth, setMorphEarth] = useState(()=>()=>console.log('morphEarth not set'));
  
  const [inVisualizeDataMode, setInVisualizeDataMode] = useState(false);

  const contextValues = {
    appWidth, setAppWidth,
    appHeight, setAppHeight,
    inEditMode, setInEditMode, 
    viewIntro, setViewIntro, 
    isExpoPlaying, setIsExpoPlaying,
    atBottomOfDoc, setAtBottomOfDoc,
    minYear, setMinYear,
    maxYear, setMaxYear,
    scrollEarth, setScrollEarth,
    removeExpoComponents, setRemoveExpoComponents,
    enterVisualizeDataMode, setEnterVisualizeDataMode,
    exitVisualizeDataMode, setExitVisualizeDataMode,
    inVisualizeDataMode, setInVisualizeDataMode,
    earthIsFlat, setEarthIsFlat,
    sectionRefs, setSectionRefs,
    // updateCrossingColors, setUpdateCrossingColors
    updateVisualizationByYear, setUpdateVisualizationByYear,
    morphEarth, setMorphEarth
  }

  return (
    <AppContext.Provider value={contextValues}>
      {children}
    </AppContext.Provider>
  );
};

export const useGlobalContext = () => {
  return useContext(AppContext);
};

// export list
// export {AppProvider, useGlobalContext};

export default AppContext;
