import React, {useContext} from 'react'
import  AppContext  from '../contexts/AppContext';
const VisualizeBtn = () => {
    const {inVisualizeDataMode, setInVisualizeDataMode, enterVisualizeDataMode, removeExpoComponents, scrollEarth} = useContext(AppContext);
    return (
        <button className="title-btn visualize-btn" onClick={()=>{
            console.log("view data");
            // remove expo components so they are not visible in viewDataMode
            window.removeEventListener('scroll', scrollEarth);
            removeExpoComponents();
            setInVisualizeDataMode(true);
            enterVisualizeDataMode();
        }}>Visualize Data</button>
    )
}

export default VisualizeBtn
