export const borderVertexShader = `

#include <morphtarget_pars_vertex>

varying vec3 fragPosition;
varying vec3 modelPosition;
varying vec3 vColor;
varying float morphInfluences;

void main() {
    #include <begin_vertex>
    #include <morphtarget_vertex>
    #include <beginnormal_vertex>
    #include <morphnormal_vertex>
    
    morphInfluences = morphTargetInfluences[0];

    vColor = vec3(color.rgb);
    // fragPosition = vec3(modelMatrix*vec4(position, 1.0));
    fragPosition = vec3(modelMatrix*vec4(transformed, 1.0));
    // modelPosition = vec3(modelMatrix * vec4(position, 1.0));
    // modelPosition = vec3(normalMatrix * vec3(position));
    modelPosition = vec3(normalMatrix * vec3(transformed));
    // gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
    gl_Position = projectionMatrix * modelViewMatrix * vec4(transformed, 1.0);
}
`;

export const borderFragmentShader = `

varying vec3 fragPosition;
varying vec3 modelPosition;
varying vec3 vColor;
varying float morphInfluences;

void main(){
    
    vec3 lightPosition1 = vec3(0, 0, -30);
    vec3 lightDir1 = normalize(lightPosition1 - modelPosition);
    float diff1 = abs(dot(modelPosition, lightDir1));
    
    // gl_FragColor = vec4(vColor.xyz, max((2.0 -(4.2 - 0.0*diff1 - 1.0*pow(diff1, 1.5)+ 0.5*pow(diff1, 2.0)*(1.0)* (0.3*1.0))) + 1.0*(diff1)*(0.0), 0.0) + (1.0*morphInfluences));
    gl_FragColor = vec4(vColor.xyz, (1.0-(4.2 - 0.0*diff1 - 1.0*pow(diff1, 1.5)+ 0.5*pow(diff1, 2.0)*(1.0)* (0.3*1.0))) + 1.0*(diff1)*(0.0) + (10.0*morphInfluences));
    
}
`;