import React, {useContext, useRef} from 'react'
import AppContext from '../contexts/AppContext.js';

const CloseVisualBtn = () => {
    
    const {inVisualizeDataMode, setInVisualizeDataMode, exitVisualizeDataMode, scrollEarth, morphEarth, earthIsFlat, setEarthIsFlat} = useContext(AppContext);
    
    const handleClick = ()=>{
        setInVisualizeDataMode(false);
        exitVisualizeDataMode();
        if (earthIsFlat) morphEarth(true);
        setEarthIsFlat(false);
        window.addEventListener('scroll', scrollEarth);
        window.scrollTo(0,0);
    }
    
    return (
        <button  className={inVisualizeDataMode? 'title-btn close-visual-btn close-visual-btn--show' : 'title-btn close-visual-btn'} onClick={handleClick}>Close</button>
        )
    }
    
    
    export default CloseVisualBtn
