import React, {useContext, useRef} from 'react'
import AppContext from '../contexts/AppContext.js';
// import { scrollEarth } from '../js/scrollEarth.js';

const IntroBtn = () => {
    
    const {viewIntro, setViewIntro, scrollEarth} = useContext(AppContext);
    const btnRef = useRef(null);
    
    const handleClick = ()=>{
        setViewIntro(!viewIntro);
    }
    
    return (
        <button  className="title-btn intro-btn" onClick={handleClick} ref={btnRef}>Introduction</button>
        )
    }
    
    export default IntroBtn
