const fragmentShader = `
    varying vec3 modelPosition;
    varying vec3 lightColor;
    uniform sampler2D globeTexture;
    uniform sampler2D nightTexture;
    varying vec2 vertexUV;
    varying vec3 vectorNormal;
    varying float morphInfluences;

    void main(){
        
        vec3 lightPosition = vec3(0, 0, -30);
        // vec3 lightPosition = vec3(-1.0*cameraPosition.x, -1.0*cameraPosition.y, -1.0*cameraPosition.z);

        vec3 lightDir = normalize(lightPosition - modelPosition);
        float diff = abs(dot(vectorNormal, lightDir));
        vec3 diffuseLight = .3*diff*lightColor;
        
        float intensity = max(0.85 - dot(vectorNormal, vec3(0.0, 0.0, 1.0)), 0.0);
        
        vec3 atmosphere = vec3(0.3, 0.6, 1.0) * pow(intensity, 1.4);
        
        vec3 result = vec3(0,0,0);
        
        result = atmosphere + (1.0*(4.2 - 5.1*diff - 0.0*pow(diff, 1.5)+ 0.4*pow(diff, 2.0))*texture2D(globeTexture, vertexUV).xyz)* diffuseLight + (1.9*diff*diff - 0.6*(diff))*texture2D(nightTexture,vertexUV).xyz + 0.125*(1.0 - morphInfluences)*texture2D(globeTexture, vertexUV).xyz; 
        
        gl_FragColor = vec4(result, 1.0);
    }`;
    
export default fragmentShader;