import * as THREE from './external_js/three.module.js'
import * as TWEEN from './external_js/tween.esm.js'
import {OrbitControls} from './external_js/OrbitControls.js';

import { scrollEarth } from './scrollEarth.js';
import {initTourComponents} from './tourComponents.js';
import getBordersAndCrossings, {min, max} from './getBordersAndCrossings.js';

// file imports
// import daymap from '../static/img/8k_earth_daymap.jpeg';
// import nightmap from '../static/img/8k_earth_nightmap.jpeg';
import daymap from '../static/img/8k_earth_daymap.jpeg';
import nightmap from '../static/img/8k_earth_nightmap.jpeg';

import { centerCrossing, addSelectedCrossing, removeDataVisComponents, cameraDistance, updateCrossingColors, updateBorderColors } from './dataVisualization.js';

// shader imports
import vertexShader from './shaders/vertexShader.js';
import fragmentShader from './shaders/fragmentShader.js';
import {atmosphereVertexShader, atmosphereFragmentShader} from './shaders/atmosphereShaders.js';
import { remove } from './external_js/tween.esm.js';

// raycasting variables
let mouse = new THREE.Vector2();
let raycaster = new THREE.Raycaster();

function onMouseMove(event) {
    
    // calculate mouse position in normalized device coordinates
    // (-1 to +1) for both components
    mouse.x = ( event.clientX / window.innerWidth ) * 2 - 1;
    mouse.y = - ( event.clientY / window.innerHeight ) * 2 + 1;
    
    // // event.buttons may or may not work for safari
    // if (event.buttons === 1) {
    //     console.log('button down');
    //     camera.position.x = mouse.x;
    //     camera.position.y = mouse.y;
    // }
}

const initialCameraPosition = 36;


// might wait till loaded to use
window.addEventListener('mousemove', onMouseMove, false);


// function for eventlistener
// async function earthExpo(canvas) {
function earthExpo(canvas) {
    
    // window.addEventListener('touchstart', ()=>{
    //     console.log('touchstart');
    // })
    
    // wrapped variable indicating whether earth is currently flat or not
    let earthIsFlat = false;
    
    // raycaster on click
    function clickBorder() {
        console.log('click');
        raycaster.setFromCamera(mouse, camera);
        const intersects = raycaster.intersectObjects([crossings]); // only look for crossings for now, can add borders later
        console.log("intersects");
        console.log(intersects);
        if (intersects[0]){
            // intersects.forEach((element)=> {
            const element = intersects[0];
            
            // determine which crossing has been clicked
            
            // get vertex face.a represents the first vertex of the face
            const faceA = element.face.a;
            console.log('faceA');
            console.log(faceA);
            // get x,y,z of vertex
            const v1 = [crossings.geometry.attributes.position.getX(faceA), crossings.geometry.attributes.position.getY(faceA), crossings.geometry.attributes.position.getZ(faceA)]
            
            // get x,y,z of flattened vertex if earth is flat
            let flatPos;
            if (earthIsFlat){
                flatPos = [crossings.geometry.morphAttributes.position[0].getX(faceA),crossings.geometry.morphAttributes.position[0].getY(faceA),crossings.geometry.morphAttributes.position[0].getZ(faceA)];
            }
            
            const matchingIDs = [];
            let j = 0; // correponds to each crossing record -- only expect two different j's to be printed
            // probably want to make this a for loop to break out of once the ID is found
            vertexCrossingIDMap.forEach((crossing)=>{
                j++;
                const vertexArray = crossing.vertices.array;
                const itemSize = crossing.vertices.itemSize;
                for (let i = 0; i < vertexArray.length; i = i + itemSize) {
                    if (vertexArray[i] === v1[0]) {
                        // x coord matches
                        if (vertexArray[i + 1] === v1[1]) {
                            // y coord matches
                            if(vertexArray[i + 2]===v1[2]){
                                // z coord matces
                                matchingIDs.push(crossing.crossingID);
                            }
                        } 
                    }
                }
            })
            
            centerCrossing(matchingIDs[0], vertexCrossingIDMap, earthGroup, scene, camera, renderer, flatPos, controls);
            // save OrbitControls state after camera has moved
            // controls.target.set(0,0,0);
            // controls.update();
            // time out so crossing isn't added until camera is in position
            setTimeout(()=>{
                // need to add flatPos to these arguments
                addSelectedCrossing(matchingIDs[0], vertexCrossingIDMap, earthGroup, scene, camera, renderer, canvas, controls, flatPos);
                renderer.render(scene, camera);
            }, 1000);
            
            
            // alert("matchingIDs = " + matchingIDs);
            // }
            // });
            
        }
    }
    
    // canvas.addEventListener('click', (e)=>{
    //     // console.log('click');
    //     console.log(e.clientX);
    //     console.log(e.clientY);
    //     clickBorder();
    // })
    
    // const gui = new dat.GUI();
    
    //initiate initial value object for scrollEarth
    
    let innerWidth = Math.round(window.innerWidth);
    let innerHeight = Math.round(window.innerHeight);
    
    //create scene
    const scene = new THREE.Scene();
    
    // create camera
    const defaultFOV = 10;
    let fov = defaultFOV;
    const aspect = innerWidth/innerHeight;
    
    // 1050/600 = 1.75
    if (aspect < 1.75 && aspect > 1){
        // adjust fov
        fov = 1.75*defaultFOV/aspect;
    } else {
        fov = defaultFOV;
    }
    
    console.log('aspect');
    console.log(aspect);
    console.log('fov');
    console.log(fov);
    
    const near = .1;
    // const far = 1000;
    const far = 64; // orbit controls max distance set to 64
    const camera = new THREE.PerspectiveCamera(fov, aspect, near, far);
    
    
    camera.position.z = initialCameraPosition; // was 8
    ;
    
    
    
    // lighting
    
    // add lighting
    const color = 0xFF0000;
    const intensity = 10;
    const light = new THREE.AmbientLight(color, intensity);
    scene.add(light);
    
    const directionColor = 0xFFFFFF;
    const directionIntensity = 1;
    const directionalLight = new THREE.DirectionalLight(directionColor, directionIntensity)
    
    directionalLight.position.set(0, 0, 0);
    
    scene.add(directionalLight);
    
    
    // const canvas = renderer.domElement;
    // create renderer
    const renderer = new THREE.WebGLRenderer({
        antialias: true,
        alpha: true,
        canvas: canvas,
    });
    
    
    
    
    renderer.setSize(innerWidth, innerHeight);
    
    console.log('window.devicePixelRatio');
    console.log(window.devicePixelRatio);
    
    renderer.setPixelRatio(window.devicePixelRatio);
    console.log('renderer.getPixelRatio()');
    console.log(renderer.getPixelRatio());
    
    // 8k earth
    
    // load texture
    const texture = new THREE.TextureLoader().load(daymap);
    texture.anisotropy = renderer.capabilities.getMaxAnisotropy();
    
    
    // night texture
    const nightTexture = new THREE.TextureLoader().load(nightmap);
    nightTexture.anisotropy = renderer.capabilities.getMaxAnisotropy();
    
    // merge uniforms with null values
    const uniforms = THREE.UniformsUtils.merge([
        {
            globeTexture: {
                value: null
            },
            nightTexture: {
                value: null
            }
        }, THREE.UniformsLib['lights']]);
        
        
        // avoids bug mentioned https://github.com/mrdoob/three.js/issues/8016
        uniforms.globeTexture.value = texture;
        uniforms.nightTexture.value = nightTexture;
        
        // const defines = {};
        //     defines['USE_MORPHTARGETS'] = '';
        // define shader material
        const shaderMaterial = new THREE.ShaderMaterial({
            vertexShader,
            fragmentShader,
            transparent: true,
            uniforms: uniforms,
            lights: true,
            morphTargets: true,
            morphNormals: true,
            // flatShading: false,
            // morphTargetsRelative: true,
            side: THREE.DoubleSide,
            // defines
        });
        // const shaderMaterial = new THREE.MeshBasicMaterial({
        //     color: 'red'
        // })
        
        
        // make sphere
        const radius = 5;
        const sphereGeometry = new THREE.PlaneBufferGeometry(4*radius, 2*radius, 128, 128);
        // sphereGeometry.translate(0,0,radius);
        
        sphereGeometry.morphAttributes.position = [];
        sphereGeometry.morphAttributes.normal = [];
        const sphereFormation = [];
        const sphereNormals = [];
        var uvs = sphereGeometry.attributes.uv;
        var uv = new THREE.Vector2();
        var t = new THREE.Vector3();
        for (let i = 0; i < uvs.count; i++){
            // calculate vertices
            uv.fromBufferAttribute(uvs, i);
            t.setFromSphericalCoords(radius, Math.PI*(1 - uv.y), Math.PI*(uv.x - .5)*2);
            sphereFormation.push(t.x,t.y,t.z);
            // calculate normals
            t.normalize();
            sphereNormals.push(t.x, t.y, t.z);
        }
        sphereGeometry.morphAttributes.position[0] = new THREE.Float32BufferAttribute(sphereFormation, 3);
        sphereGeometry.morphAttributes.normal[0] = new THREE.Float32BufferAttribute(sphereNormals, 3);
        // const sphere = new THREE.Mesh(new THREE.SphereBufferGeometry(radius, 128, 128), shaderMaterial)
        const sphere = new THREE.Mesh(sphereGeometry, shaderMaterial);
        // sphere.position.z = radius;
        sphere.morphTargetInfluences[0] = 1; // start as sphere
        
        const atmosphereUniforms = {
            u_opacity: {value: 1.0}
        };
        
        // make second sphere for atmosphere
        const atmosphereShaderMaterial = new THREE.ShaderMaterial({
            vertexShader: atmosphereVertexShader,
            fragmentShader: atmosphereFragmentShader,
            blending: THREE.AdditiveBlending,
            side: THREE.BackSide,
            transparent: true,
            uniforms: atmosphereUniforms,
            depthWrite: false,
        });
        
        const atmosphereGeometry = new THREE.SphereBufferGeometry(radius*1.3, 64, 64)
        
        const atmosphere = new THREE.Mesh(atmosphereGeometry, atmosphereShaderMaterial);
        // atmosphere.updateMorphTargets();
        // atmosphere.morphTargetInfluences[0] = 0;
        // const atmosphere = new THREE.Mesh(new THREE.PlaneBufferGeometry(1.3*4*radius, 1.3*2*radius, 128, 128), atmosphereShaderMaterial);
        
        
        // stars
        const starGeometry = new THREE.BufferGeometry();
        const starMaterial = new THREE.PointsMaterial({ color: 0xFFFFFF });
        
        const starVertices = [];
        
        
        const stars = new THREE.Points(starGeometry, starMaterial);
        
        for (let i = 0; i < 100000; i++) {
            
            const theta = 2 * Math.PI * (Math.random());
            const phi = Math.PI * (Math.random());
            const r = (2000 - 300) * Math.random() + 300;
            
            const x = r * Math.cos(theta) * Math.sin(phi);
            const y = r * Math.cos(phi);
            const z = r * Math.sin(theta) * Math.sin(phi);
            starVertices.push(x, y, z);
        }
        
        starGeometry.setAttribute('position', new THREE.Float32BufferAttribute(starVertices, 3));
        
        scene.add(stars);
        
        // group earth and atmosphere
        const earthGroup = new THREE.Group();
        // const [borders, crossings, vertexCrossingIDMap] =  await getBordersAndCrossings(radius);
        const [borders, crossings, vertexCrossingIDMap] =  getBordersAndCrossings(radius);
        earthGroup.add(sphere);
        earthGroup.add(atmosphere);
        earthGroup.add(borders);
        earthGroup.add(crossings);
        
        
        const initialX = -1.65;
        const initialY = -1.9;
        earthGroup.position.x = initialX;
        earthGroup.position.y = initialY;
        
        scene.add(earthGroup);
        
        // const initialRotationY = 3 * Math.PI / 2 - .25;
        const initialRotationY = 0;
        const initialRotationX = .35;
        
        earthGroup.rotation.x += initialRotationX;
        earthGroup.rotation.y += initialRotationY;
        
        // const [introParagraph1Mesh, introParagraph2Mesh] = initTourComponents();
        
        // scene.add(introParagraph1Mesh);
        // scene.add(introParagraph2Mesh);
        
        const introGroup = new THREE.Group();
        
        
        // const [introParagraph1Mesh, introParagraph2Mesh, circleMesh, imgMesh] = initTourComponents(radius,vertexCrossingIDMap);
        const introComponents = initTourComponents(radius,vertexCrossingIDMap);
        
        introGroup.add(introComponents.introParagraph1Mesh);
        // introGroup.add(introComponents.introParagraph2Mesh);
        
        // scene.add(introGroup);
        introComponents.introGroup = introGroup;
        
        // render scene when all js has been executed
        // window.onload = ()=> renderer.render(scene, camera); // this listener is most likely added after loading in when earthExpo.js is async 
        
        // check if page has already loaded
        if (performance.getEntriesByType("navigation").every((e) => e.loadEventEnd)){
            // window loaded
            renderer.render(scene, camera)
        } else {
            // window not loaded already
            window.onload = ()=> renderer.render(scene, camera);
        }
        
        const initialPositions = {
            cameraPositionX: camera.position.x,
            cameraPositionY: camera.position.y,
            cameraPositionZ: camera.position.z,
            initialEarthRotX: earthGroup.rotation.x,
            initialEarthRotY: earthGroup.rotation.y,
            initialEarthRotZ: earthGroup.rotation.z
        }
        
        // window.addEventListener('scroll', ()=>{
        
        //     // update scene with scrollEarth
        //     scrollEarth(scene,earthGroup, camera, introComponents, renderer);
        //     // rerender scene
        //     renderer.render(scene, camera);
        // } );
        
        
        
        //resize
        function onWindowResize() {
            
            // resizeCanvas();
            const width = window.innerWidth;
            const height = window.innerHeight;

            const aspect = width / height;
            
            if (aspect < 1.75 && aspect > 1){
                // adjust fov
                fov = 1.75*defaultFOV/aspect;
            } else {
                fov = defaultFOV;
            }
            
            // console.log('aspect');
            // console.log(aspect);
            // console.log('fov');
            // console.log(fov);
            
            camera.aspect = aspect;
            camera.fov = fov;
            camera.updateProjectionMatrix();
            
            renderer.setSize(width, height);
            // setCanvasDimensions(renderer.domElement, width, height);
            
            renderer.render(scene, camera);
        }
        
        window.addEventListener("resize", onWindowResize);
        
        // add OrbitControls to scene
        const controls = new OrbitControls(camera, renderer.domElement);
        controls.enabled = false;
        controls.minDistance = 16;
        controls.maxDistance = 64;
        // controls.target.clamp(new THREE.Vector3(-3*Math.PI*radius, -1.5*Math.PI*radius, 0), new THREE.Vector3(3*Math.PI*radius, 1.5*Math.PI*radius, 0));
        controls.target.clamp(new THREE.Vector3(-1, -1, 0), new THREE.Vector3(1, 1, 0));
        // controls.update();
        controls.addEventListener( 'change', ()=> { renderer.render(scene, camera)} );
        
        
        // window.addEventListener('click',()=>{
        //     updateCrossingColors(2000, crossings, vertexCrossingIDMap)
        //     renderer.render(scene, camera);
        // });
        
        
        /* FUNCTIONS TO RETURN TO APP TO ADD AS ONCLICKS */
        
        // wrapper for scrollEarth that also renders
        function scrollEarthHelper(){
            // if (!lastCall) lastCall = Date.now();
            // update scene with scrollEarth
            scrollEarth(scene,earthGroup, camera, introComponents, renderer);
            // // log stats
            // console.log("Scene polycount:", renderer.info.render.triangles)
            // console.log("Active Drawcalls:", renderer.info.render.calls)
            // console.log("Textures in Memory", renderer.info.memory.textures)
            // console.log("Geometries in Memory", renderer.info.memory.geometries)
            // rerender scene
            renderer.render(scene, camera);
            // const ellapsed = Date.now() - lastCall;
            // if (ellapsed > 1000/30) renderer.render(scene, camera);
            // lastCall = Date.now();   
        }
        
        // function to remove expo components
        function removeExpoComponents() {
            const {introParagraph1Mesh, xing1Components, xing2Components, xing3Components, concludingParagraphMesh, introGroup} = introComponents;
            // remove introGroup - need to do both of these for some reason
            // introGroup.remove(introParagraph1Mesh);
            scene.remove(introGroup);
            // remove xing1Components
            Object.keys(xing1Components).forEach((component)=>{
                earthGroup.remove(xing1Components[component]);
            })
            scene.remove(xing1Components.paragraphMesh);
            //remove each xing2Components
            Object.keys(xing2Components).forEach((component)=>{
                earthGroup.remove(xing2Components[component]);
            })
            // remove each xing3Component
            Object.keys(xing3Components).forEach((component)=>{
                earthGroup.remove(xing3Components[component]);
            })
            scene.remove(xing3Components.paragraphMesh);
            // remove concluding paragraph mesh
            scene.remove(concludingParagraphMesh);
            console.log('after removal');
            console.log(scene.children);
            
            // reposition earth and camera
            camera.position.x = initialPositions.cameraPositionX;
            camera.position.y = initialPositions.cameraPositionY;
            camera.position.z = initialPositions.cameraPositionZ;
            earthGroup.rotation.x = initialPositions.initialEarthRotX;
            earthGroup.rotation.y = initialPositions.initialEarthRotY;
            earthGroup.rotation.z = initialPositions.initialEarthRotZ;
        }
        
        function enterVisualizeDataMode(){
            // tweens
            const earthTween = new TWEEN.Tween(earthGroup.position).to({x: 0, y: 0, z:0}, 1000);
            const earthRotTween = new TWEEN.Tween(earthGroup.rotation).to({x: 0, y:0, z: 0});
            const cameraTween = new TWEEN.Tween(camera.position).to({x: 0, y: 0, z:cameraDistance}, 1000);
            
            
            function animate(){
                const req = requestAnimationFrame(animate);
                earthTween.onComplete(()=>{
                    cancelAnimationFrame(req);
                })
                earthTween.update();
                earthRotTween.update();
                cameraTween.update();
                renderer.render(scene, camera);
            }
            
            animate();
            earthTween.start();
            earthRotTween.start();
            cameraTween.start();
            
            // enable OrbitControls
            controls.enabled = true;
            
            // make crossings clickable
            canvas.addEventListener('click', clickBorder
            )
            
            //scroll to top
            window.scrollTo(0,0);
        }
        function exitVisualizeDataMode(){
            
            // tweens
            const earthTween = new TWEEN.Tween(earthGroup.position).to({x: initialX, y: initialY, z:0}, 1000);
            const earthRotTween = new TWEEN.Tween(earthGroup.rotation).to({x: initialRotationX, y: initialRotationY, z:0}, 1000);
            const cameraTween = new TWEEN.Tween(camera.position).to({x: 0, y: 0, z:initialCameraPosition}, 1000);
            
            function animate(){
                const req = requestAnimationFrame(animate);
                earthTween.onComplete(()=>{
                    cancelAnimationFrame(req);
                    // disable orbitControls when animation is done
                    controls.enabled = false;
                })
                earthTween.update();
                earthRotTween.update();
                cameraTween.update();
                camera.lookAt(0,0,0);
                renderer.render(scene, camera);
            }
            
            animate();
            earthTween.start();
            earthRotTween.start();
            cameraTween.start();
            
            if (earthIsFlat) morphEarth(earthIsFlat);
            
            // remove any current visualized borders
            removeDataVisComponents(scene, earthGroup);
            
            // disallow clicking of crossings
            canvas.removeEventListener('click', clickBorder
            )
            renderer.render(scene, camera);
        }
        
        // // wrapper to pass updateCrossingColors to app
        // function updateCrossingColorsWrapper(year){
        //     updateCrossingColors(year, crossings);
        //     renderer.render(scene, camera);
        // }
        
        // // wrapper to pass updateCrossingColors to app
        // function updateCrossingColorsWrapper(year){
        //     updateCrossingColors(year, crossings);
        //     renderer.render(scene, camera);
        // }
        
        function updateVisualizationByYear(year){
            updateCrossingColors(year, crossings);
            updateBorderColors(year, borders);
            renderer.render(scene, camera);
        }
        
        function morphEarth(startedFlat){
            
            // remove active crossing components
            removeDataVisComponents(scene, earthGroup);
            
            // make sure controls are active
            controls.enabled = true;
            
            const duration = 1000; // 1 sec
            
            // set three js state variable
            earthIsFlat = !startedFlat;
            
            let tarX, tarY, tarZ;
            if (startedFlat) {
                // get current values of the control target for manual tween
                tarX = controls.target.x;
                tarY = controls.target.y;
                tarZ = controls.target.z;
            }
            
            let start;
            const cameraTween = new TWEEN.Tween(camera.position).to({x: 0, y: 0, z: 48}, duration);
            
            function animate(){
                // console.log('morph animate');
                if (!start) start = Date.now();
                const ellapsed = Date.now() - start;
                if (ellapsed < duration) {
                    requestAnimationFrame(animate);
                    if (startedFlat) {
                        // morphing to sphere
                        sphere.morphTargetInfluences[0] = ellapsed/duration;
                        crossings.morphTargetInfluences[0] = 1 - ellapsed/duration;
                        // borders.children.forEach((border)=>{
                        //     border.morphTargetInfluences[0] = 1 - ellapsed/duration;
                        // })
                        
                        borders.morphTargetInfluences[0] = 1 - ellapsed/duration;
                        
                        atmosphereUniforms.u_opacity.value = ellapsed/duration;
                        // manually tween where camera is looking from OrbitControls target to the origin
                        camera.lookAt(tarX*(1 - ellapsed/duration), tarY*(1 - ellapsed/duration),tarZ*(1 - ellapsed/duration))
                    } else {
                        // morphing to flat
                        sphere.morphTargetInfluences[0] = 1.0 - ellapsed/duration;
                        crossings.morphTargetInfluences[0] = ellapsed/duration;
                        // borders.children.forEach((border)=>{
                        //     border.morphTargetInfluences[0] = ellapsed/duration;
                        // })
                        
                        borders.morphTargetInfluences[0] = ellapsed/duration;
                        
                        atmosphereUniforms.u_opacity.value = 1.0 - ellapsed/duration;
                        cameraTween.update();
                        camera.lookAt(0,0,0);
                    }
                    
                } else {
                    // transition should be done
                    if (startedFlat) {
                        sphere.morphTargetInfluences[0] = 1.0;
                        crossings.morphTargetInfluences[0] = 0.0;
                        // borders.children.forEach((border)=>{
                        //     border.morphTargetInfluences[0] = 0.0;
                        // })
                        
                        borders.morphTargetInfluences[0] = 0.0;
                        
                        atmosphereUniforms.u_opacity.value = 1.0;
                        // ensure controls are targetting origin for sphere
                        controls.target.set(0,0,0);
                    } else {
                        sphere.morphTargetInfluences[0] = 0.0;
                        crossings.morphTargetInfluences[0] = 1.0;
                        // borders.children.forEach((border)=>{
                        //     border.morphTargetInfluences[0] = 1.0;
                        // })
                        
                        borders.morphTargetInfluences[0] = 1.0;
                        
                        atmosphereUniforms.u_opacity.value = 0.0;
                    }
                }
                
                // sphere.material.needsUpdate = true;
                atmosphere.material.needsUpdate = true;
                renderer.render(scene, camera);
            }
            if (earthIsFlat) {
                cameraTween.start();
            }
            animate();
            
            
            if (earthIsFlat){
                controls.enableRotate = false;
                controls.mouseButtons.LEFT = THREE.MOUSE.PAN;
            } else {
                controls.enableRotate = true;
                controls.mouseButtons.LEFT = THREE.MOUSE.ROTATE;
            }
            // disable OrbitControls rotate and reposition camera
        }
        
        
        // return function so eventlistener can be removed in useEffect cleanup
        return {scrollEarthHelper, removeExpoComponents, enterVisualizeDataMode, exitVisualizeDataMode, updateVisualizationByYear, morphEarth, min, max};
        
    }
    
    
    
    
    
    
    export default earthExpo;
    // export {removeExpoComponents};