// react core imports
import React from 'react';
import { useEffect, useRef, useContext } from 'react';

// react imports
import AppContext from '../contexts/AppContext.js';
import RotateDevice from './RotateDevice.js';

// external imports 
import earthExpo from '../js/earthExpo.js';


const ThreeDEnv = () => {

    const {appHeight, appWidth, setRemoveExpoComponents, setEnterVisualizeDataMode, setExitVisualizeDataMode, setScrollEarth, scrollEarth, viewIntro, inVisualizeDataMode, setUpdateVisualizationByYear, setMorphEarth, setMinYear, setMaxYear} = useContext(AppContext);

    const canvas = useRef(null);

    // useEffect shouldn't be async to avoid race conditions
    useEffect( () => {
        // functions from earthExpo
        const {scrollEarthHelper, removeExpoComponents, enterVisualizeDataMode, exitVisualizeDataMode, updateVisualizationByYear, morphEarth, min, max} = earthExpo(canvas.current); 

        // set min and max year
        setMinYear(min);
        setMaxYear(max);

        // set global state variables to retrieved functions
        setScrollEarth(()=>scrollEarthHelper);
        setRemoveExpoComponents(()=>removeExpoComponents);
        setEnterVisualizeDataMode(()=> enterVisualizeDataMode)
        setExitVisualizeDataMode(()=> exitVisualizeDataMode)
        // setUpdateCrossingColors(()=> updateCrossingColorsWrapper);
        setUpdateVisualizationByYear(()=> updateVisualizationByYear);
        setMorphEarth(()=> morphEarth);

        // add scrollEarth eventListener
        window.addEventListener('scroll', ()=>{
            console.log('scroll')
            scrollEarthHelper();
        });

        return ()=> {
            window.removeEventListener('scroll', scrollEarthHelper);
        }
    }, []);

    // add scrollEarth eventListener if viewIntro
    // useEffect(()=>{
    //     if (viewIntro) window.addEventListener('scroll', scrollEarth);
    //     return ()=>{
    //         window.scrollTo(0,0);
    //         window.removeEventListener('scroll', scrollEarth);
    //     }
    // },[viewIntro])

    return (
        <>
        {(viewIntro || inVisualizeDataMode) && appHeight > appWidth ? <RotateDevice></RotateDevice>:null}
        <canvas className={viewIntro? 'disable-pointer-events' : null}ref={canvas}>

        </canvas>
        </>
    )
}

export default ThreeDEnv;
