export const crossingFragmentShader = `

varying vec3 fragPosition;
varying vec3 modelPosition;
varying vec3 vColor;
varying vec4 color4;
varying float morphInfluences;

void main(){
    
    vec3 lightPosition1 = vec3(0, 0, -30);
    vec3 lightDir1 = normalize(lightPosition1 - modelPosition);
    float diff1 = abs(dot(modelPosition, lightDir1));
    
    gl_FragColor = vec4(vColor.xyz, max((2.0 -(4.2 - 0.0*diff1 - 1.0*pow(diff1, 1.5)+ 0.5*pow(diff1, 2.0)*(1.0)* (0.3*1.0))) + 1.0*(diff1)*(0.0), 0.0) + (1.0*morphInfluences));
    
}
`;