            const p2 = this.points[floorIndex + 1];
            const dx = p2[0] - p1[0];
            const dy = p2[1] - p1[1];
            const dz = p2[2] - p1[2];
            tx = p1[0] + delta*dx;
            ty = p1[1] + delta*dy;
            tz = p1[2] + delta*dz;
