import React, {useContext} from 'react'
import AppContext from '../contexts/AppContext';

const AutoplayBtn = () => {
    // view intro
    const {viewIntro, isExpoPlaying, setIsExpoPlaying, atBottomOfDoc} = useContext(AppContext);
    // state to indicate whether exposition is playing or not -- should this be here? if not where?
    return (
        <button className={(viewIntro && !atBottomOfDoc)? 'title-btn autoplay-btn autoplay-btn--show' : 'title-btn autoplay-btn'} onClick={()=>setIsExpoPlaying(!isExpoPlaying)}>
            {isExpoPlaying? 'Pause' : 'Autoplay'}
        </button>
    )
}

export default AutoplayBtn
