function getFirstLastYear(){
    let min = Number.MAX_SAFE_INTEGER;
    let max = 0;
    crossingData.forEach((dataPoint)=>{
        if (dataPoint.year > max) max = dataPoint.year;
        if (dataPoint.year < min) min = dataPoint.year;
    })
    return {max, min};
}

export const {max, min} = getFirstLastYear();

// used synchronous
function loadSVG(string){
    const svgLoader = new SVGLoader();
    const data = svgLoader.parse(string);
    return data;
}

