    function genThetaAttributes(crossingID, geometry){
        // filter data by id
        const filteredData = crossingData.filter((item)=>{
            return item.borderid === crossingID;
        });
        // strip out second crossing
        const usedBorderYears = new Set();
        const uniqueCrossingData = [];
        filteredData.forEach((item)=>{
            if (!usedBorderYears.has(item.year)){
                uniqueCrossingData.push(item);
                usedBorderYears.add(item.year);
            }
        });
        // add BufferAttribute for each year
        for (let i = min; i <= max; i++ ){
            const arr = uniqueCrossingData.filter((item)=>{
                return item.year === i;
            })
            
            const numVerts = geometry.getAttribute('position').count;
            const itemSize = 3; // r, g, b
            const colorArray = new Uint8Array(itemSize * numVerts);
            
            if (arr.length > 0){
                const xing = arr[0]; 
                // crossing has data for this year
                //get color
                const { forestGreen, limeGreen, paleGreen, yellow, orange, red, white } = colors;
                const theta = xing.theta;
                let color;
                if (theta <= -2) {
                    color = forestGreen;
                } else if (theta >-2 && theta <= -1) {
                    color = limeGreen;
                } else if (theta > -1 && theta <= 0) {
                    color = paleGreen;
                } else if (theta >0 && theta <= 1) {
                    color = yellow;
                } else if (theta > 1 && theta <= 2) {
                    color = orange;
                } else if (theta > 2) {
                    color = red;
                } else {
                    color = white;
                }
                
                const rgb = [(color%0x01000000 - color%0x00001000)/0x00010000, (color%0x00010000 - color%0x00000010)/0x00000100, color%0x00000100];
                
                //  add color
                colorArray.forEach((v, index)=> {
                    colorArray[index] = rgb[index % 3];
                })
            } else {
                // crossing does not have data for this year
                colorArray.forEach((v,index)=>{
                    colorArray[index] = 0;
                })
            }
            const normalized = true;
            const colorAttrib = new THREE.BufferAttribute(colorArray, itemSize, normalized);
            geometry.setAttribute(`color${i}`, colorAttrib);
        }
    }
    
