    function genXiProperties(borderID, geometry){
        const filteredData = borderData.filter((item)=>{
            return item.dyad === borderID;
        });
        
        // strip out second border
        const usedBorderYears = new Set();
        const uniqueCrossingData = [];
        filteredData.forEach((item)=>{
            if (!usedBorderYears.has(item.year)){
                uniqueCrossingData.push(item);
                usedBorderYears.add(item.year);
            }
        });
        
        // const yearColorXiMap = {};
        // add get color for each year
        for (let i = min; i <= max; i++ ){
            const arr = uniqueCrossingData.filter((item)=>{
                return item.year === i;
            })
            let color;
            if (arr.length > 0){
                // data for that year
                const border = arr[0]; // only 1 element
                color = getColorFromXi(border.xi);
            } else {
                // no data for that year
                color = 0xFFFFFF;
            }
             // make new buffer attrbiute
                const rgb = [(color%0x01000000 - color%0x00001000)/0x00010000, (color%0x00010000 - color%0x00000010)/0x00000100, color%0x00000100];
                const numVerts = geometry.getAttribute('position').count;
                const itemSize = 3; // r, g, b
                const colorArray = new Uint8Array(itemSize * numVerts);
                
                colorArray.forEach((v, index)=> {
                    colorArray[index] = rgb[index % 3];
                })
                const normalized = true;
                const colorAttrib = new THREE.BufferAttribute(colorArray, itemSize, normalized);
                geometry.setAttribute(`color${i}`, colorAttrib);
        }
    }
    
    function gen2DXingCoordinates(crossingsGeometry,radius){
        
        // const geometry = crossings.geometry;
        const geometry = crossingsGeometry;
        // const position = crossings.geometry.attributes.position;
        const position = crossingsGeometry.attributes.position;
        geometry.morphAttributes.position = [];
        geometry.morphAttributes.normal = [];
        const flatCoordinates = [];
        const flatNormals = [];
        
        
        for (let i = 0; i < position.count; i++){
            const pos = new THREE.Vector3();
            pos.fromBufferAttribute(position, i);
            pos.normalize();
            // [0,1] uvs
            const u = (.5 + Math.atan2(pos.x,pos.z)/(2*Math.PI));
            const v = (.5 - Math.asin(pos.y)/Math.PI);
            
            // x, y, z
            const x = 4*radius*(u - .5);
            const y = -2*radius*(v - .5);
            const z = 0.01;
            flatCoordinates.push(x, y, z);
            //normals
            flatNormals.push(0,0,-1);
        }
        
        geometry.morphAttributes.position[0] = new THREE.Float32BufferAttribute(flatCoordinates, 3);
        geometry.morphAttributes.normal[0] = new THREE.Float32BufferAttribute(flatNormals, 3);
        // crossings.morphTargetInfluences[0] = 0; // start spherical
        
    }
    
