    // helper functions
    
    // determine border color based on xi
    function getColorFromXi(xi){
        
        const { forestGreen, limeGreen, paleGreen, yellow, orange, red, white } = colors;
        
        let color;
        if (xi <= -2) {
            color = forestGreen;
        } else if (xi >-2 && xi <= -1) {
            color = limeGreen;
        } else if (xi > -1 && xi <= 0) {
            color = yellow;
        } else if (xi >0 && xi <= 1) {
            color = orange;
        } else if (xi > 1 && xi <= 2) {
            color = red;
        } else if (xi > 2) {
            color = red;
        } else {
            color = white;
        }
        
        return color;
    }
    
    function setColorOnBufferGeometry(geometry, color){
        const rgb = [(color%0x01000000 - color%0x00001000)/0x00010000, (color%0x00010000 - color%0x00000010)/0x00000100, color%0x00000100];
        
        
        const numVerts = geometry.getAttribute('position').count;
        const itemSize = 3; // r, g, b
        const colors = new Uint8Array(itemSize * numVerts);
        
        colors.forEach((v, index)=> {
            colors[index] = rgb[index % 3];
        })
        
        const normalized = true;
        const colorAttrib = new THREE.BufferAttribute(colors, itemSize, normalized);
        geometry.setAttribute('color', colorAttrib);
    }
    
    export default getBordersAndCrossings;