import React, {useContext, useRef, useState, useEffect, useCallback} from 'react';

// component imports
import ThreeDEnv from './components/ThreeDEnv.js';
import Nav from './components/Nav.js';
import LandingContent from './components/LandingContent.js';
import HomeContent from './components/HomeContent.js'
import ResearchSection from './components/Research/ResearchSection.js';
import Footer from './components/Footer.js';

//context import
import AppContext, { useGlobalContext } from './contexts/AppContext.js';

// css
import './App.css';
import './static/fontawesome-free-5.15.3-web/css/all.css';

function App() {
  
  const {viewIntro, inVisualizeDataMode, appWidth, setAppWidth, setAppHeight, isExpoPlaying, setIsExpoPlaying, atBottomOfDoc, setAtBottomOfDoc} = useGlobalContext();
  
  // node represents node with ref = {appRef}
  const [node, setNode] = useState(null);
  
  // set up ref here and use to find width of <App>, send as prop to landing content
  
  // useCallback hook because ref can't be null on first render
  const appRef = useCallback(
    (node) => {
      if (node!== null)setNode(node);
    },[]);
    
    // resize event handler
    const handleResize = ()=>{
      // setAppWidth(node.getBoundingClientRect().width);
      // setAppHeight(node.getBoundingClientRect().height);
      setAppWidth(window.innerWidth);
      setAppHeight(window.innerHeight);
    }

    // function to chekc scroll position and bottom of doc to make scroll-icon appear/disappear
    const checkScrollPosition = ()=>{
      const position = window.scrollY;
      if (position >= window.document.body.getBoundingClientRect().height - window.innerHeight){
        setAtBottomOfDoc(true);
      } else {
        setAtBottomOfDoc(false);
      }
    }
    
    useEffect(()=>{
      
      // set appWidth if node is defined
      if (node) setAppWidth(node.getBoundingClientRect().width);
      
      // add resize event handler
      window.addEventListener('resize', 
      handleResize);

      // add scroll event listner
      window.addEventListener('scroll', checkScrollPosition);
      
      // remove previous eventlisteners before subsequent renders
      return ()=> {
        window.removeEventListener('resize', handleResize);
        window.removeEventListener('scroll', checkScrollPosition);
      }
    },[node]);
    
    const rafRef = useRef(null);
    // animate function for scrolling
    function animate(){
      const speed = 1.75; //px
      
        rafRef.current = requestAnimationFrame(animate);
        window.scrollTo(0, window.scrollY + speed);
        if (window.scrollY >= window.document.body.getBoundingClientRect().height - window.innerHeight){
          // rafRef.current = null;
          setIsExpoPlaying(false);
        }
      // } 
    }
    
    // this effect runs whenever isExpoPlaying is is changed, and kicks off the animation if it is changed to true
    useEffect(()=>{
      if (isExpoPlaying) {
        rafRef.current = requestAnimationFrame(animate);
      }
      return ()=> {
        cancelAnimationFrame(rafRef.current);
      }
    }, [isExpoPlaying])
    
    
    return (
      <div className={`App ${viewIntro ? "view-intro" : ""}`} ref={appRef}>
      <ThreeDEnv></ThreeDEnv>
      <Nav></Nav>
      <LandingContent appWidth={appWidth}></LandingContent>
      {!inVisualizeDataMode && !viewIntro? <>
      <HomeContent></HomeContent>
      {/* <ResearchSection></ResearchSection> */}
      <Footer></Footer></> : null}
      </div>
      );
    }
    
    export default App;
