import * as THREE from './external_js/three.module.js';
import * as TWEEN from './external_js/tween.esm.js';

import {initTourComponents, genParagraphMesh, genCircleMesh, genCrossingPicture, genGlowMesh, getGrowableLine} from './tourComponents.js'
import { loadCrossing, removeCrossing } from './scrollEarth.js';
import {colors} from './getBordersAndCrossings.js';

import crossingData from '../data/temporal_crossing_data.json';

export const cameraDistance = 48;

export const centerCrossing = function (borderID, vertexCrossingIDMap, earthGroup, scene, camera, renderer, flatPos, controls) {
    
    console.log('center crossing');
    
    let cameraPos;
    if (flatPos){
        // earth is flat
        cameraPos = new THREE.Vector3(flatPos[0], flatPos[1], cameraDistance - 12);
    } else {
        // earth is round
        vertexCrossingIDMap.forEach((crossing)=>{
            
            if (crossing.crossingID === borderID){
                // console.log(crossing.vertices.array); // equivalent to a BufferGeometry.attributes.position.array
                const array = crossing.vertices.array;
                
                // get first vertex
                const vertex = new THREE.Vector3(array[0],array[1], array[2]);
                
                const vertexWorld = earthGroup.localToWorld(vertex); // if problems check here that the transformations are right
                
                // find where camera should be using vertex postion localToWorld and calculating a vector in that direction of length (radius + distanceToCamera)
                
                const unitVector = vertexWorld.normalize();
                
                cameraPos = unitVector.multiplyScalar(cameraDistance); // scalar is distance from camera to origin
                
                
            }
        })
    }
    
    const x = cameraPos.x;
    const y = cameraPos.y;
    const z = cameraPos.z;
    
    //tween camera.position to {x, y, z}
    const cameraTween = new TWEEN.Tween(camera.position).to({x: x, y: y, z: z}, 1000);
    
    function animate(){
        const req = requestAnimationFrame(animate);
        cameraTween.onComplete(()=>{
            // add selected crossing when movement is complete
            if (flatPos){
                // if flat, set reset target and set up
                controls.target.set(flatPos[0],flatPos[1],0);
                controls.update();
            }
            cancelAnimationFrame(req);
        })
        
        // update all tweens here
        cameraTween.update();
        // make camera look at globe
        if(!flatPos) camera.lookAt(0,0,0); // should still go if not flat
        renderer.render(scene, camera);
    }
    cameraTween.start();
    animate(); 
}


const addedComponents = []; // array for tracking added components

export const removeDataVisComponents = function(scene, earthGroup){
    console.log("removeDataVisComponents");
    console.log(addedComponents);
    addedComponents.forEach((component)=>{
        scene.remove(component);
        earthGroup.remove(component);
    })
}

// adds selected crossing to map with the 3D or 2D orientation depending on whether flatPos is passed in as an argument or not
export const addSelectedCrossing = function (borderID, vertexCrossingIDMap, earthGroup, scene, camera, renderer, canvas, controls, flatPos){
    
    console.log('flatPos in addSelectedCrossing');
    console.log(flatPos);
    
    //disable OrbitControls
    controls.enabled = false;
    
    const circleMesh = genCircleMesh(borderID, vertexCrossingIDMap, flatPos);
    const box = new THREE.Mesh(new THREE.PlaneGeometry(1.5,1), new THREE.MeshBasicMaterial({color: 0xFFFFFF}));
    const boxLocalPosition = new THREE.Vector3(-1.25, 0.5, -18);
    const boxWorldPosition = camera.localToWorld(boxLocalPosition);
    
    box.lookAt(camera.position.x,camera.position.y,camera.position.z);
    box.scale.x = .001;
    box.scale.y = .001;
    box.scale.z = .001;
    box.position.x = boxWorldPosition.x;
    box.position.y = boxWorldPosition.y;
    box.position.z = boxWorldPosition.z;
    
    // scene.add(box);
    
    const boxEarthPosition = earthGroup.worldToLocal(boxWorldPosition); // converts doesn't return new vector!!!
    
    // generate line
    const from = [circleMesh.position.x, circleMesh.position.y, circleMesh.position.z];
    const to = [boxEarthPosition.x, boxEarthPosition.y,boxEarthPosition.z];
    const line = getGrowableLine(from, to, 1);
    
    const xingComponents = {
        circleMesh: circleMesh, imgMesh: box, glowMesh: undefined, line: line, paragraphMesh: undefined, 
    };
    
    // track added components
    addedComponents.push(circleMesh, box, line);
    
    loadCrossing(scene, earthGroup, renderer, camera, xingComponents, undefined, false);
    
    function removeHelper(){
        // reenable controls
        controls.enabled = true;
        // remove crossing
        removeCrossing(scene, earthGroup, renderer, camera, xingComponents, undefined, false);
        // untrack components
        
        for (const prop in xingComponents) {
            addedComponents.filter((component)=>{
                return component !== xingComponents[prop];
            })
        }
        
        // remove eventListener with this function
        canvas.removeEventListener('click', removeHelper);
    }
    
    canvas.addEventListener('click', removeHelper);
    
}


