import React, { useState, useContext, useEffect } from "react";
import AppContext from "../contexts/AppContext";

const Nav = () => {
  const [showNav, setShowNav] = useState(false);
  const {sectionRefs} = useContext(AppContext);
  
  // nav onClicks
  
  // useEffect(()=>{
  
  // }, [])
  
  const clickHome = ()=>{
    window.scrollTo({
      top: 0,
      left: 0,
      behavior: 'smooth'
    });
    
  }
  const clickResearch = ()=>{
    sectionRefs.research.current.scrollIntoView({behavior: 'smooth'});
  }
  
  console.log('sectionRefs');
  console.log(sectionRefs);
  
  const toggleNav = () => {
    setShowNav(!showNav);
  };
  return (
    <>
    <div className="nav-icon" onClick={toggleNav}>
    <span className="fas fa-bars"></span>
    </div>
    <div className={showNav ? "nav nav--show" : "nav"}>
    <ul className="nav-links">
    <li className="nav-link" onClick={clickHome}>Home</li>
    <li className="nav-link" onClick={clickResearch}>Research</li>
    <li className="nav-link">Data</li>
    <li className="nav-link">People</li>
    <li className="nav-link">Media</li>
    </ul>
    </div>
    : ''
    </>
    );
  };
  
  export default Nav;
