// const initialEarthRotY = 3 * Math.PI / 2 - .25;
const initialEarthRotY = 0;
const initialEarthRotZ = 0;

// pos1 final states
const pos1CameraX = initialCameraX + .0026*pos1;
const pos1CameraY = initialCameraY + -.0018*pos1;
const pos1CameraZ = initialCameraZ + .029*pos1;

const pos1IntroGroupY = initialIntroGroupY + -.0018*pos1 +.0021*pos1;
const pos1IntroGroupX = initialIntroGroupX + .0026*pos1;
const pos1IntroGroupZ = initialIntroGroupZ + .029*pos1;

// pos2 final states
const pos2CameraX = pos1CameraX;
const pos2CameraY = pos1CameraY;
const pos2CameraZ = pos1CameraZ;

const pos2IntroGroupX = pos1IntroGroupX;
const pos2IntroGroupY = pos1IntroGroupY +.0021*(pos2 - pos1);
const pos2IntroGroupZ = pos1IntroGroupZ;

// pos3 final states
const pos3CameraX = pos2CameraX - .00015*(pos3 - pos2);
const pos3CameraY = pos2CameraY + .00015*(pos3 - pos2);
const pos3CameraZ = pos2CameraZ - .003*(pos3 - pos2);

// pos5 final states
const pos5CameraX = pos3CameraX -.0078*(pos4 - pos3);
const pos5CameraY = pos3CameraY + .00015*(pos4 - pos3);
const pos5CameraZ = pos3CameraZ -.012*(pos4 - pos3);

const pos5EarthRotX = initialEarthRotX + .00012*(pos3 - pos2);
const pos5EarthRotY = initialEarthRotY - .00024*(pos3 - pos2);
const pos5EarthRotZ = initialEarthRotZ;

// pos6 final states

const pos6CameraX = pos5CameraX + .0064*(pos6 - pos5);
const pos6CameraY = pos5CameraY + .00035*(pos6 - pos5);
const pos6CameraZ = pos5CameraZ -.007*(pos6 - pos5); 

const pos6EarthRotX = pos5EarthRotX - .00138*(pos6 - pos5);
const pos6EarthRotY = pos5EarthRotY + .00256*(pos6 - pos5);
const pos6EarthRotZ = pos5EarthRotZ;

// pos7 final states

const pos7CameraX = pos6CameraX;
const pos7CameraY = pos6CameraY;
const pos7CameraZ = pos6CameraZ; 

const pos7EarthRotX = pos6EarthRotX;
const pos7EarthRotY = pos6EarthRotY;
const pos7EarthRotZ = pos6EarthRotZ;

// pos8 final states

const pos8EarthRotY = pos7EarthRotY + .00448*(pos8 - pos7);
const pos8EarthRotX = pos7EarthRotX + 2*Math.sqrt( .00207*(pos8 - pos7)) - .00207*(pos8 - pos7);
const pos8EarthRotZ = pos7EarthRotZ;

const pos8CameraX = pos7CameraX + .32*Math.pow(.0036*(pos8 - pos7), 2);
const pos8CameraY = pos7CameraY - 1.5*Math.pow(.00064*(pos8 - pos7), 2);
const pos8CameraZ = pos7CameraZ + .0045*(pos8 - pos7); 

// pos9 final states

const pos9CameraX = pos8CameraX;
const pos9CameraY = pos8CameraY;
const pos9CameraZ = pos8CameraZ; 

const pos9EarthRotX = pos8EarthRotX;
const pos9EarthRotY = pos8EarthRotY;
const pos9EarthRotZ = pos8EarthRotZ;

// pos10 final states

const pos10CameraX = pos8CameraX - .0026*(pos10-pos9);
const pos10CameraY = pos8CameraY + .0014*(pos10-pos9);
const pos10CameraZ = pos8CameraZ + .008*(pos10-pos9);

const pos10EarthRotX = pos8EarthRotX;
const pos10EarthRotY = pos8EarthRotY;
const pos10EarthRotZ = pos8EarthRotZ;

