function scrollEarth (scene, earthGroup, camera, introComponents, renderer) {
    
    // make sure camera rotation is 0
    camera.rotation.x = initialCameraRotX;
    camera.rotation.y = initialCameraRotY;
    camera.rotation.z = initialCameraRotZ;
    
    // destructure introComponents to get components
    const {introGroup,xing1Components, xing2Components, xing3Components} = introComponents;
    
    // scroll, and when you get to a point of no return call the appropriate transition function
    
    // first movement - scroll up of intro paragraphs 1 and 2
    const offset = window.pageYOffset;
    // console.log('offset');
    // console.log(offset);
    if (offset <= pos1) {
        // make sure introGroup is added
        scene.add(introGroup);
        // camera zooms back and moves to the right so earth is taking up roughly half of the view port
        // camera
        camera.position.x = initialCameraX + .0026*offset;
        camera.position.y = initialCameraY + -.0018*offset;
        camera.position.z = initialCameraZ + .029*offset;
        // intro paragraphs
        introGroup.position.x = initialIntroGroupX + .0026*offset;
        introGroup.position.y = initialIntroGroupY + -.0018*offset +.0021*offset;
        introGroup.position.z = initialIntroGroupZ + .029*offset;
        
    } else if (offset > pos1 && offset <= pos2){
        // camera waits, intro paragraphs scroll
        introGroup.position.y = pos1IntroGroupY +.0021*(offset - pos1);
        
    } else if (offset > pos2 && offset <= pos3) {
        // intro paragraph continues to scroll earth rotates slightly in the y- and x-directions
        earthGroup.rotation.y = initialEarthRotY - .00024*(offset - pos2);
        earthGroup.rotation.x = initialEarthRotX + .00012*(offset - pos2);
        // camera zooms in slightly (and paragraphs adjust accordingly)
        camera.position.z = pos2CameraZ - .003*(offset - pos2);
        camera.position.x = pos2CameraX - .00015*(offset - pos2);
        camera.position.y = pos2CameraY + .00015*(offset - pos2);
        //intro paragraph:
        introGroup.position.x = pos2IntroGroupX - .00015*(offset - pos2);
        introGroup.position.y = pos2IntroGroupY +.0021*(offset - pos2)+ .00015*(offset - pos2);
        introGroup.position.z = pos2IntroGroupZ - .003*(offset - pos2);
        
    }
    else if (offset > pos3 && offset < pos4 ) {
        // camera zooms back in and moves to the right, earth rotates ever so slightly to the right, getting Norway-Russia in view
        camera.position.x = pos3CameraX -.0078*(offset - pos3);
        camera.position.y = pos3CameraY + .00015*(offset - pos3);
        camera.position.z = pos3CameraZ -.012*(offset - pos3);
        // remove crossing if it is there
        if (xing1Components.isLoaded) removeCrossing(scene, earthGroup, renderer, camera, xing1Components);
        // be sure paragraph is there for next xing
        scene.add(xing1Components.paragraphMesh);
    } else if (offset >= pos4 && offset <= pos5){
        // load xing1 if it is not loaded already
        if (!xing1Components.isLoaded) {
            loadCrossing(scene, earthGroup, renderer, camera, xing1Components,xing1Values, true);
        }
        // add back intro group in case it has been removed
        scene.add(introGroup);
    } else if (offset > pos5 && offset <= pos6){
        
        // remove xing1 if necessary
        if (xing1Components.isLoaded){
            removeCrossing(scene, earthGroup, renderer, camera, xing1Components);
        }
        // remove xing2 if necessary
        if (xing2Components.isLoaded){
            removeCrossing(scene, earthGroup, renderer, camera, xing2Components);
        }
        
        earthGroup.rotation.y = pos5EarthRotY + .00256*(offset - pos5);
        earthGroup.rotation.x = pos5EarthRotX - .00138*(offset - pos5);
        
        camera.position.x = pos5CameraX + .0064*(offset - pos5);
        camera.position.y = pos5CameraY + .00035*(offset - pos5);
        camera.position.z = pos5CameraZ -.007*(offset - pos5); 
        // add paragraph mesh so it is already in view when xing2 is loaded
        earthGroup.add(xing2Components.paragraphMesh);
        // xing2Components.paragraphMesh.lookAt(xing2Values.cameraX, xing2Values.cameraY, xing2Values.cameraZ);
        // scene.add(xing2Components.paragraphMesh);
    } else if (offset > pos6 && offset <= pos7) {
        
        if (!xing2Components.isLoaded) {
            loadCrossing(scene, earthGroup, renderer, camera, xing2Components,xing2Values, true);
            // scene.add(xing2Components.paragraphMesh);
        }
        // remove  oman-uae paragraph
        scene.remove(xing3Components.paragraphMesh);
        
    } else if (offset > pos7 && offset <= pos8) {
        scene.remove(introGroup);
        scene.add(xing3Components.paragraphMesh);
        if (xing2Components.isLoaded) removeCrossing(scene, earthGroup, renderer, camera, xing2Components);
        if (xing3Components.isLoaded) removeCrossing(scene, earthGroup, renderer, camera, xing3Components);
        
        earthGroup.rotation.y = pos6EarthRotY + .00448*(offset - pos7);
        earthGroup.rotation.x = pos6EarthRotX + 2*Math.sqrt( .00207*(offset - pos7)) - .00207*(offset - pos7);
        
        
        camera.position.x = pos6CameraX + .32*Math.pow(.0036*(offset - pos7), 2);
        camera.position.y = pos6CameraY - 1.5*Math.pow(.00064*(offset - pos7), 2);
        camera.position.z = pos6CameraZ + .0045*(offset - pos7); 
        
    } else if (offset > pos8 && offset <= pos9){
        if (!xing3Components.isLoaded) loadCrossing(scene, earthGroup, renderer, camera, xing3Components,xing3Values, true)
        // make sure paragraph is positioned correctly
        xing3Components.paragraphMesh.position.x = xing3ParagraphMeshInitVal.x;
        xing3Components.paragraphMesh.position.y = xing3ParagraphMeshInitVal.y;
        xing3Components.paragraphMesh.position.z = xing3ParagraphMeshInitVal.z;
        // //make sure earth is in proper position
        // earthGroup.rotation.x = xing3Values.earthRotX;
        // earthGroup.rotation.y = xing3Values.earthRotY;
        // earthGroup.rotation.z = xing3Values.earthRotZ;
        
        // //make sure camera is in proper position
        // camera.position.x = xing3Values.cameraX;
        // camera.position.y = xing3Values.cameraY;
        // camera.position.z = xing3Values.cameraZ;
        
        // // log camera position to determine starting points for xing components
        // console.log('camera position');
        // console.log("x = " + camera.position.x);
        // console.log("y = " + camera.position.y);
        // console.log("z = " + camera.position.z);
        
        
        // const imgMesh = introComponents.xing3Components.imgMesh;
        // const imgMeshPosition = new THREE.Vector3(imgMesh.position.x, imgMesh.position.y, imgMesh.position.z);
        // const glowMesh = introComponents.xing3Components.glowMesh;
        // const glowMeshPosition = new THREE.Vector3(glowMesh.position.x, glowMesh.position.y, glowMesh.position.z);
        
        // console.log("imgMesh earthgroup coords");
        // console.log(earthGroup.worldToLocal(imgMeshPosition));
        // // imgMesh.lookAt(camera.position.x, camera.position.y, camera.position.z);
        // // console.log("img rotation");
        // // console.log("x: " + imgMesh.rotation.x);
        // // console.log("y: " + imgMesh.rotation.y);
        // // console.log("z: " + imgMesh.rotation.z);
        // // console.log("glowMesh earthgroup coords");
        // // console.log(earthGroup.worldToLocal(glowMeshPosition));
        // // glowMesh.lookAt(camera.position.x, camera.position.y, camera.position.z);
        // // console.log("glowMesh rotation");
        // // console.log("x: " + glowMesh.rotation.x);
        // // console.log("y: " + glowMesh.rotation.y);
        // // console.log("z: " + glowMesh.rotation.z);
        
    } else if (offset > pos9 && offset <= pos10) {
        // remove xing3 if it is loaded
        if (xing3Components.isLoaded) removeCrossing(scene, earthGroup, renderer, camera, xing3Components);
        
        // camera.position.x = pos8CameraX - .0052*(offset-pos9);
        // camera.position.y = pos8CameraY + .0027*(offset-pos9);
        // camera.position.z = pos8CameraZ + .016*(offset-pos9);
        
        // const xing3ParagraphMesh = xing3Components.paragraphMesh;
        
        // xing3ParagraphMesh.position.x = xing3ParagraphMeshInitVal.x + .01000*(offset-pos9);
        camera.position.x = pos8CameraX - .0026*(offset-pos9);
        camera.position.y = pos8CameraY + .0014*(offset-pos9);
        camera.position.z = pos8CameraZ + .008*(offset-pos9);
        
        const xing3ParagraphMesh = xing3Components.paragraphMesh;
        
        xing3ParagraphMesh.position.x = xing3ParagraphMeshInitVal.x + .02000*(offset-pos9);
        
        scene.remove(introComponents.concludingParagraphMesh)
        
    } else if (offset > pos10 && offset <= pos11) {
        // console.log("pos10 camera");
        // console.log("x: " + pos10CameraX);
        // console.log("y: " + pos10CameraY);
        // console.log("z: " + pos10CameraZ);
        const concludingParagraphMesh = introComponents.concludingParagraphMesh;
        scene.add(concludingParagraphMesh);
        concludingParagraphMesh.position.y =  concludingParagraphMeshInitVal.y + .0021*(offset - pos10)
        earthGroup.rotation.y = pos10EarthRotY - .00024*(offset - pos10);
        // console.log('offset');
        // console.log(offset);
        // console.log("ypos");
        // console.log(concludingParagraphMesh.position.y);
    };
    
    //render scene
    // renderer.render(scene, camera);
}

function loadCrossing(scene, earthGroup, renderer, camera, xingComponents, xingValues, isExpo){
    
    const {circleMesh, imgMesh, glowMesh, line, paragraphMesh} = xingComponents;
    
    let cameraPos;
    
    // only do if xingValues are provided in tour
    if (xingValues) {
        
        const {cameraX, cameraY, cameraZ} = xingValues;
        const {earthRotX, earthRotY, earthRotZ} = xingValues;
        
        // cameraPos =  new THREE.Vector3(cameraX, cameraY, cameraZ);
        
        // there is still some delay in the below

        //make sure earth is in proper position
        earthGroup.rotation.x = earthRotX;
        earthGroup.rotation.y = earthRotY;
        earthGroup.rotation.z = earthRotZ;
        
        //make sure camera is in proper position
        camera.position.x = cameraX;
        camera.position.y = cameraY;
        camera.position.z = cameraZ;
    } else {
        // cameraPos = new THREE.Vector3(camera.position.x, camera.position.y, camera.position.z);
    }
    
    //add picture
    if(isExpo) {
        
        // scene.add(imgMesh);
        earthGroup.add(imgMesh);
        imgMesh.lookAt(camera.position.x, camera.position.y, camera.position.z);
        
    } else {
        // console.log('!expo')
        scene.add(imgMesh);
        
        // imgMesh.lookAt(camera.position.x, camera.position.y, camera.position.z);
    }
    
    // add glow
    if(glowMesh) {
        // scene.add(glowMesh);
        earthGroup.add(glowMesh);
        glowMesh.lookAt(camera.position.x, camera.position.y, camera.position.z);
        
    } else {
        // scene.add(imgMesh);
    }
    
    //add circle around crossing
    earthGroup.add(circleMesh);
    
    //add line from crossing to picture
    earthGroup.add(line);
    //expand picture/glow to full size
    const imgTween = new TWEEN.Tween(imgMesh.scale).delay(250).to({x: 1, y: 1, z: 1}, 250);
    let glowTween;
    if (glowMesh) glowTween = new TWEEN.Tween(glowMesh.scale).delay(250).to({x: 1, y: 1, z: 1}, 250);
    
    // //add paragraph
    // scene.add(paragraphMesh);
    
    // get num vertices for tube geometry
    const nMax = line.geometry.getIndex().count;
    
    let start;
    function animate(){
        if (!start) start = Date.now();
        const req = requestAnimationFrame(animate);
        imgTween.onComplete(()=>{
            cancelAnimationFrame(req);
            // console.log('imgMesh position');
            // console.log(earthGroup.worldToLocal(new THREE.Vector3(imgMesh.position.x, imgMesh.position.y, imgMesh.position.z)));
            // console.log('glowMesh position');
            // console.log(earthGroup.worldToLocal(new THREE.Vector3(glowMesh.position.x, glowMesh.position.y, glowMesh.position.z)));
            // console.log('paragraph position');
            // console.log(earthGroup.worldToLocal(new THREE.Vector3(paragraphMesh.position.x, paragraphMesh.position.y, paragraphMesh.position.z)));
            // // paragraphMesh.lookAt(xingValues.cameraX, xingValues.cameraY, xingValues.cameraZ);
            // console.log('paragraph rotation');
            // console.log('x: ' + paragraphMesh.rotation.x);
            // console.log('y: ' + paragraphMesh.rotation.y);
            // console.log('z: ' + paragraphMesh.rotation.z);
        })
        // for line growth
        const ellapsed = Date.now() - start;
        line.geometry.setDrawRange(0, nMax*ellapsed/250);
        
        // update all tweens here, add delays as needed to be in sync
        imgTween.update();
        if (glowTween) glowTween.update();
        renderer.render(scene, camera);
    }
    imgTween.start();
    if (glowTween) glowTween.start();
    
    animate();
    
    // indicate crossing is loaded
    xingComponents.isLoaded = true;
}

function removeCrossing(scene, earthGroup, renderer, camera, xingComponents, isExpo){
    
    const {circleMesh, imgMesh, glowMesh, line, paragraphMesh} = xingComponents;
    
    // define tweens here
    const imgTween = new TWEEN.Tween(imgMesh.scale).to({x: .01, y: .01, z: .01}, 250);
    let glowTween;
    if(glowMesh) glowTween = new TWEEN.Tween(glowMesh.scale).to({x: .01, y: .01, z:.01},250);
    
    // get number of indices
    const nMax = line.geometry.getIndex().count;
    
    let start;
    function animate(){
        if (!start) start = Date.now();
        const req = requestAnimationFrame(animate);
        imgTween.onComplete(()=>{
            // cancel animations frame
            cancelAnimationFrame(req);
            // remove meshes
            earthGroup.remove(circleMesh);
            earthGroup.remove(glowMesh);
            earthGroup.remove(line);
            if (earthGroup.children.includes(imgMesh))
            {
                // exposition
                earthGroup.remove(imgMesh);
            } else {
                // visualization
                scene.remove(imgMesh);
            }
        })
        // for line growth
        const ellapsed = Date.now() - start;
        line.geometry.setDrawRange(0, nMax - nMax*ellapsed/250);
        // update all tweens here, add delays as needed to be in sync
        imgTween.update();
        if(glowMesh)glowTween.update();
        // render scene
        renderer.render(scene, camera);
    }
    // start tweens
    imgTween.start();
    if(glowMesh)glowTween.start();
    //animate
    animate();
    // indicate crossing1 is not loaded
    xingComponents.isLoaded = false;
}

export {scrollEarth, loadCrossing, removeCrossing};